<template>
    <div style="padding-top: 2px;">
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="项目组名称" prop="project_team_id">
                <el-select v-model="searchFormData.project_team_id"
                           placeholder="请选择项目组"
                           clearable
                           @change="project_team_change"
                >
                    <el-option v-for="item in project_team_code_list"
                               :key="item._id"
                               :label="item.project_team_name"
                               :value="item._id">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="项目名称" prop="project_id">
                <el-select v-model="searchFormData.project_id"
                           placeholder="请选择项目"
                           clearable
                >
                    <el-option v-for="item in project_selected_code_list"
                               :key="item._id"
                               :label="item.project_name"
                               :value="item._id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="创建时间" prop="createTime">
                <el-date-picker
                        v-model="searchFormData.createTime"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[14, 20, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper"
                    >
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="onAdd(module_name)"><i class="iconfont icon-xinzeng"></i> 新增</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)">
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column
                    prop="createTime"
                    label="创建时间"
                    width="156">
            </el-table-column>
            <el-table-column
                    :formatter="format_project_team"
                    :show-overflow-tooltip="true"
                    prop="project_team_id"
                    label="项目组名称"
                    width="200">
            </el-table-column>
            <el-table-column
                    :formatter="format_project"
                    :show-overflow-tooltip="true"
                    prop="project_id"
                    label="项目名称"
                    width="200">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="update_server_ip"
                    label="更新服务器ip"
                    width="120">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="update_server_port"
                    label="更新服务器端口"
                    width="120">
            </el-table-column>
            <!--<el-table-column-->
                    <!--:show-overflow-tooltip="true"-->
                    <!--prop="update_structure"-->
                    <!--label="更新结构"-->
                    <!--width="220">-->
            <!--</el-table-column>-->
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="project_update_summary"
                    label="项目更新简介"
                    width="220">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="remark_bak_path"
                    label="标记备份文件路径"
            >
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="update_share_bak_path"
                    label="更新备份路径"
            >
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="160">
                <template slot-scope="scope">
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="重建远程配置文件" placement="left">
                            <el-button @click.native.prevent="onRebuildRemoteConf(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-gengxinpeizhi"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="查看" placement="left">
                            <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-chakan"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="修改" placement="left">
                            <el-button @click.native.prevent="onEdit(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-xiugai"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="删除" placement="left">
                            <el-button @click.native.prevent="onDelete(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-shanchu icon-red"></i></el-button>
                        </el-tooltip>
                    </el-col>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'projectUpdateConfList',
        data() {
            return {
                module_name: 'projectUpdateConf',
                refresh_page_method: 'refresh_' + this.module_name,
                project_code_list: [],
                project_team_code_list: [],
                project_selected_code_list: [],
                searchFormData: {
                    pageNum: 1,
                    pageSize: 14,
                    pageCount: 1,
                    order: '+_id'
                },
                tableData: []
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.search();
            });
        },
        async mounted() {
            this.project_code_list = await this.$getProjectCodeInfo();
            this.project_team_code_list = await this.$getProjectTeamCodeInfo();
            this.search();
        },
        methods: {
            async project_team_change(project_team_id)/*项目组选择变更*/ {
                this.project_selected_code_list = await this.$getProjectCodeInfo({'project_team_id': project_team_id});
                if (this.searchFormData.project_id !== undefined) {
                    this.searchFormData.project_id = '';
                }
            },
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            format_project(row, column, cellValue) {
                return this.$formatCodeValue(row, this.project_code_list, column.property, '_id', 'project_name')
            },
            format_project_team(row, column, cellValue) {
                return this.$formatCodeValue(row, this.project_team_code_list, column.property, '_id', 'project_team_name')
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            async search() {
                let data = await this.$search(this.searchFormData, 'OAMP_search_project_update_conf_info_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data
            },
            async onDelete(index, rows)  // 删除
            {
                let rowData = rows[index];
                if (this.$del(rowData, 'OAMP_delete_project_update_conf_info')) {
                    rows.splice(index, 1);
                    this.$message.success('删除数据成功');
                    this.search();
                }
                else {
                    this.$message.error('删除数据失败');
                }
            },
            async onRebuildRemoteConf(index, rows)/*重建远程配置文件*/ {
                let data = await this.$exec(rows[index], 'OAMP_rebuild_remote_update_conf');
                if (data != null && data.is_success === '1') {
                    this.$message.success('更新远程配置文件成功');
                    let res_data = data.data;
                    let msg = '<i>重建成功服务器共计<strong><i>' + res_data.success_count + '</i></strong>个<br>成功ip为:<br>';
                    res_data.success_ips.forEach((data) => {
                        msg += '[<strong><i>' + data + '</i></strong>]<br>'
                    });
                    msg += '</i>';
                    this.$notify({title: '远程重建信息', dangerouslyUseHTMLString: true, message: msg, offset: 300});
                }
                else if (data != null && data.is_success === '0') {
                    this.$message.error('更新远程配置文件异常')
                    let res_data = data.data;
                    let msg = '<strong><i>重建服务器总数' + res_data.exec_count + '个</i></strong><br>';
                    msg += '<i>重建成功服务器共计<strong><i>' + res_data.success_count + '</i></strong>个<br>成功ip为:<br>';
                    res_data.success_ips.forEach((data) => {
                        msg += '[<strong><i>' + data + '</i></strong>]<br>'
                    });
                    msg += '</i>';
                    msg += '<i>重建失败服务器共计<strong><i>' + res_data.failed_count + '</i></strong>个<br>失败ip为:<br>';
                    res_data.failed_ips.forEach((data) => {
                        msg += '[<strong><i>' + data + '</i></strong>]<br>'
                    });
                    msg += '</i>';
                    this.$notify({title: '远程重建信息', dangerouslyUseHTMLString: true, message: msg, offset: 300});
                }

            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



