<template>
    <el-dialog title="项目更新配置信息"
               width="1800"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name,'pageDataRef')"
    >
        <el-form :model="pageData" :rules="pageDataRules" ref="pageDataRef" :inline="true" label-width="160px" size="small">

            <el-form-item label="项目组名称" prop="project_team_id">
                <el-select v-model="pageData.project_team_id"
                           placeholder="请选择项目组"
                           clearable
                           @change="project_team_change"
                >
                    <el-option v-for="item in project_team_code_list"
                               :key="item._id"
                               :label="item.project_team_name"
                               :value="item._id">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="项目名称" prop="project_id">
                <el-select v-model="pageData.project_id"
                           placeholder="请选择项目"
                           clearable
                >
                    <el-option v-for="item in project_code_list"
                               :key="item._id"
                               :label="item.project_name"
                               :value="item._id">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="更新文件路径" prop="update_path">
                <el-input v-model="pageData.update_path" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="标记备份文件路径" prop="remark_bak_path">
                <el-input v-model="pageData.remark_bak_path" style="width:696px"></el-input>
            </el-form-item>

            <el-form-item label="代理服务器ip" prop="update_server_ip">
                <el-input v-model="pageData.update_server_ip"></el-input>
            </el-form-item>
            <el-form-item label="代理服务器ssh端口" prop="update_server_port">
                <el-input v-model="pageData.update_server_port"></el-input>
            </el-form-item>

            <el-form-item label="代理redisIP" prop="redis_ip">
                <el-input v-model="pageData.redis_ip"></el-input>
            </el-form-item>

            <el-form-item label="代理redis端口" prop="redis_port">
                <el-input v-model="pageData.redis_port"></el-input>
            </el-form-item>
            <el-form-item label="代理redis密码" prop="redis_password">
                <el-input v-model="pageData.redis_password"></el-input>
            </el-form-item>
            <el-form-item label="代理服务地址" prop="agent_service_url">
                <el-input v-model="pageData.agent_service_url" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="upstream文件路径" prop="upstream_conf_file">
                <el-input v-model="pageData.upstream_conf_file" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="更新备份路径" prop="update_share_bak_path">
                <el-input v-model="pageData.update_share_bak_path" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="更新备份配置文件路径" prop="update_share_conf_bak_path">
                <el-input v-model="pageData.update_share_conf_bak_path" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="项目更新简介" prop="project_update_summary">
                <el-input type="textarea" v-model="pageData.project_update_summary" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="备注" prop="remark">
                <el-input type="textarea" v-model="pageData.remark" style="width:696px"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose(module_name,'pageDataRef')">关 闭</el-button>
            <el-button v-if="pageInfo.pageModel === 'add'" type="primary" @click="onSubmit">确 定</el-button>
            <el-button v-else-if="pageInfo.pageModel === 'edit'" type="primary" @click="onUpdate">更 新</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        name: 'projectUpdateConfInfo',
        data() {
            return {
                module_name: 'projectUpdateConf',
                refresh_page_method: 'refresh_' + this.module_name,
                project_code_list: [],
                project_team_code_list: [],
                pageData: {},
                pageDataRules: {
                    project_team_id: [{required: true, message: '请选择项目组', trigger: 'blur'}],
                    project_id: [{required: true, message: '请选择项目', trigger: 'blur'}],
                    update_path: [{required: true, message: '请输入更新项目资源路径', trigger: 'blur'}],
                    remark_bak_path: [{required: true, message: '请输标记备份资源路径', trigger: 'blur'}],
                    update_server_ip: [{required: true, message: '请输入资源备份远程服务器ip', trigger: 'blur'}],
                    update_server_port: [{required: true, message: '请输入资源备份远程服务器端口', trigger: 'blur'}],
                    redis_ip: [{required: true, message: '请输入代理redisIP', trigger: 'blur'}],
                    redis_password: [{required: true, message: '请输入代理redis密码', trigger: 'blur'}],
                    redis_port: [{required: true, message: '请输入代理redis端口', trigger: 'blur'}],
                    upstream_conf_file: [{required: true, message: '请输入upstream文件路径', trigger: 'blur'}],
                    agent_service_url: [{required: true, message: '请输入远程服务器更代理服务地址', trigger: 'blur'}],
                    update_share_bak_path: [{required: true, message: '请输入远程服务器更新资源备份路径', trigger: 'blur'}],
                }
            }
        },
        computed: {
            pageInfo() {
                return this.$setPageInfo(this.module_name);
            }
        },
        async mounted() {
            this.project_team_code_list = await this.$getProjectTeamCodeInfo();
        },
        methods: {
            async project_team_change(project_team_id)/*项目组选择变更*/ {
                this.project_code_list = await this.$getProjectCodeInfo({'project_team_id': project_team_id});
                if (this.pageData.project_id !== undefined) {
                    this.pageData.project_id = '';
                }
            },
            async onSubmit() // 提交按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.$insert(item, 'OAMP_insert_project_update_conf_info');
                        if (res != null) {
                            _this.$message.success('新增信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('新增信息失败');
                        }
                    }
                });
            },
            async onUpdate() // 更新按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.$update(item, 'OAMP_update_project_update_conf_info');
                        if (res != null) {
                            _this.$message.success('修改信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('修改信息失败');
                        }
                    }
                });
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-input {
        width: 260px;
    }

    .el-select {
        width: 260px;
    }

    .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 260px;
    }

</style>


